/**
 * Entry point del cliente para hidrataci贸n SSR
 * Importa todos los componentes necesarios para la hidrataci贸n
 */

console.log(" mjo-litui client components loaded for hydration");

// Funci贸n para inicializar tema despu茅s de hidrataci贸n
function initializeTheme(): void {
    const savedTheme = localStorage.getItem("mjo-theme") || "light";
    const themeComponent = document.querySelector("mjo-theme");

    if (themeComponent) {
        (themeComponent as any).theme = savedTheme;
        console.log(" Theme initialized from client:", savedTheme);
    }

    const toggleBtn = document.querySelector(".theme-toggle");
    if (toggleBtn) {
        toggleBtn.textContent = savedTheme === "dark" ? "锔" : "";
    }
}

// Funci贸n global para toggle de tema (llamada desde HTML)
(window as any).toggleTheme = function (): void {
    const themeComponent = document.querySelector("mjo-theme");
    if (themeComponent) {
        const currentTheme = (themeComponent as any).theme;
        const newTheme = currentTheme === "light" ? "dark" : "light";
        (themeComponent as any).theme = newTheme;
        localStorage.setItem("mjo-theme", newTheme);

        const toggleBtn = document.querySelector(".theme-toggle");
        if (toggleBtn) {
            toggleBtn.textContent = newTheme === "dark" ? "锔" : "";
        }

        console.log(" Theme changed to:", newTheme);
    } else {
        console.warn("锔 mjo-theme component not found");
    }
};

// Inicializar tema cuando el DOM est茅 listo
document.addEventListener("DOMContentLoaded", () => {
    console.log(" DOM ready, initializing theme...");
    initializeTheme();
});

// Tambi茅n inicializar despu茅s de un breve delay para asegurar hidrataci贸n
setTimeout(() => {
    initializeTheme();
}, 100);
